
import store from './store'
import jpushIM from "./static/js/im.js"
import Vue from 'vue'
import {
	mapMutations
} from 'vuex'

export default {
	created() {
		//获取所有产品列表用以前端渲染
		var _this = this
		var list = []
		uni.request({
			url: this.$baseUrl.baseUrl + '/getProductlist',
			data: "lalla",
			success: function(e){
				for(var i=0;i<e.data.data.length;i++){
					if(e.data.data[i].houseOK == true){
						list.push(e.data.data[i])
					}
				}
				_this.saveProductlist(list)		
				
			}
		})











































































	},
	onLaunch: function() {
		var _this =this
		console.log(_this.$baseUrl.baseUrl)
		console.log('App Launch')
		uni.getSystemInfo({
			success: function(e) {










				Vue.prototype.StatusBar = e.statusBarHeight;
				let custom = wx.getMenuButtonBoundingClientRect();
				Vue.prototype.Custom = custom;
				Vue.prototype.CustomBar = custom.bottom + custom.top - e.statusBarHeight;






			}
		})
		uni.getStorage({ //获得保存在本地的用户信息
			key: 'userInfo',
			success: (e) => {
				uni.request({
					url: _this.$baseUrl.baseUrl + '/checklogin',
					sslVerify: false,
					header: {
						"Content-Type": "application/x-www-form-urlencoded",
						"Authorization": "Bearer"
					},
					data: {
						"userId": e.data.userId,
						"token": e.data.token
					},
					method: "POST",
					success: (e) => {
						console.log(e.data)
						if (e.data.err_code === 0) {
							console.log('更新登录状态')
							_this.login(e.data);
						}
					}
				})
			}
		})

	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
	methods: {
		...mapMutations(['login']),
		...mapMutations(['saveProductlist'])
	}
}
